import './UpdatePassword.css'
import React, {useState} from 'react'
import {updatePassword} from '../../../services/ApiClient'
import InputWithLabel from '../../Form/InputWithLabel/InputWithLabel'
import Button from '../../Form/FormButton/FormButton'
import {useHistory} from 'react-router-dom'
import {useFormState} from '../../../hooks/useFormState'

export default function UpdatePassword({user}) {



    const {state, onBlur, onChange} = useFormState(
        {
            data: {
                id: user.id,
                password: "",
                newpassword: ""
            },
            error: {
                password: true,
                newpassword: true
            },
            touch: {},
        },
        {
            password: v => v.length,
            newpassword: v => v.length
        }
    )

    const history = useHistory()

    const [message, setMessage] = useState('')
    const [registerError, setRegisterError] = useState(null)

    const {data, error, touch} = state

    const updatePass = async (event) => {

        event.preventDefault()

        try {
            await updatePassword(data)
            document.querySelector('.message').classList.remove('d-none')
            document.querySelector('.alert').classList.add('d-none')
            setMessage('La contrase√±a ha sido actualizada')
            setTimeout(() => {
                document.querySelector('.message').classList.add('d-none')
                history.push('/biopsias')
            }, 3000)
        } catch (err) {
            setRegisterError(err.response?.data?.message)
        }
    }

    const isError = Object.values(error).some(err => err)

    return (
        <>
            <section className="container head-bg"></section>
            <div className="container-fluid my-info">
                <div className="row justify-content-center">
                    <div className="col-sm-6 col-xl-4 col-11 login-block">
                        <h1>Crea tu nueva contrase√±a</h1>
                        {!message &&
                            <>
                                <p className="mb-5">Hola {user.name}! necesitamos que te tomes unos minutos y actualices tu contrase√±a, recuerda que esta debe tener m√≠nimo 6 caracteres y debe contener al menos un n√∫mero y una letra may√∫scula.</p>

                                <form onSubmit={updatePass}>

                                    <InputWithLabel
                                        value={data.password}
                                        onBlur={onBlur}
                                        onChange={onChange}
                                        name="password"
                                        type="password"
                                        label="Introduce tu contrase√±a actual"
                                        className={`form-control ${touch.password && error.password ? "is-invalid" : ""}`}
                                        placeholder="Ingresa nueva contrase√±a"

                                    />

                                    <InputWithLabel
                                        value={data.newpassword}
                                        onBlur={onBlur}
                                        onChange={onChange}
                                        name="newpassword"
                                        type="password"
                                        label="Introduce tu nueva contrase√±a"
                                        className={`form-control ${touch.newpassword && error.newpassword ? "is-invalid" : ""}`}
                                        placeholder="Repite la contrase√±a"
                                    />

                                    <Button
                                        type="submit"
                                        className="Button Button__enter"
                                        disabled={isError}
                                    >Actualizar contrase√±a</Button>
                                </form>
                            </>
                        }
                        {registerError && <div className="alert alert-danger">{registerError}</div>}
                        <div className="message d-none text-center">{message ? message : ''}</div>
                    </div>
                </div>
            </div>
        </>
    )
}
